a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

button {
  border: none;
  padding: 0;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #e8e8e2;
  font-family: PP Neue Montreal;
}

.container {
  max-width: 365px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (width >= 768px) {
  .container {
    width: 734px;
    max-width: none;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1270px;
    max-width: none;
  }
}

.header {
  height: 96px;
  padding: 24px 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  fill: #010101;
  border-radius: 9px;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 60px;
  transition: background, fill, all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__logo:hover {
  fill: #e8e8e2;
  background: #6d8434;
}

.header__logo:hover .header__logo--circle {
  background-color: #e8e8e2;
}

.header__logo:hover .header__logo--icon {
  fill: #6d8434;
}

.header__logo:focus {
  fill: #e8e8e2;
  background: #6d8434;
  outline: none;
}

.header__logo:focus .header__logo--circle {
  background-color: #e8e8e2;
}

.header__logo:focus .header__logo--icon {
  fill: #6d8434;
}

.header__logo--circle {
  background-color: #6d8434;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  transition: background .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__logo--icon {
  fill: #e8e8e2;
  width: 20px;
  height: 20px;
  transition: fill .3s cubic-bezier(.4, 0, .2, 1);
}

.header__logo--text {
  width: 155.026px;
  height: 20.616px;
}

.header__nav {
  justify-content: center;
  align-items: center;
  gap: 24px;
  display: flex;
}

.header__link {
  background-color: #6d8434;
  border-radius: 30px;
  width: 104px;
  padding: 12px 28px;
  transition: background .3s cubic-bezier(.4, 0, .2, 1);
}

.header__link:hover {
  background-color: #586f1f;
}

.header__link:focus {
  background-color: #586f1f;
  outline: none;
}

.header__cart {
  fill: #e8e8e2;
  color: #010101;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 48px;
  padding: 0 8px;
  transition: background, fill, all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__cart:hover {
  fill: #586f1f;
  color: #e8e8e2;
  background: #586f1f;
}

.header__cart:hover .header__cart--circle {
  background-color: #e8e8e2;
}

.header__cart:focus {
  fill: #586f1f;
  color: #e8e8e2;
  background: #586f1f;
  outline: none;
}

.header__cart:focus .header__cart--circle {
  background-color: #e8e8e2;
}

.header__cart--circle {
  background-color: #6d8434;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  transition: background .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__cart--icon {
  width: 18px;
  height: 18px;
}

.header__cart--link {
  text-transform: uppercase;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 133.333%;
  transition: color .3s cubic-bezier(.4, 0, .2, 1);
}

.header__home {
  color: #e8e8e2;
  font-size: 18px;
  font-weight: 500;
  line-height: 133.333%;
}

.hero {
  padding: 53px 0;
}

.hero__container {
  position: relative;
}

.hero__title {
  color: #010101;
  letter-spacing: -1.6px;
  text-align: center;
  margin-bottom: 20px;
  font-size: 80px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%;
}

.hero__highlight {
  color: #6d8434;
}

.hero__text {
  color: #010101cc;
  text-align: center;
  max-width: 492px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 133.333%;
}

.hero__img {
  border-radius: 60px;
  width: 1240px;
  height: 350px;
}

.hero__circle {
  cursor: default;
  background-color: #fdc152;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  display: flex;
  position: absolute;
  top: 138px;
  right: 20px;
}

.hero__circle--text {
  width: 90px;
  height: 90px;
  animation: 10s linear infinite spin;
}

.hero__circle--text:hover {
  animation-play-state: paused;
}

.hero__icon {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 38.5px;
  left: 38.5px;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.scrollToTop {
  background-color: #6d8434;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 65px;
  height: 65px;
  transition: background, box-shadow, all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  bottom: 20px;
  right: calc(50% - 650px);
}

.scrollToTop:hover {
  background-color: #e8e8e2;
  box-shadow: 0 0 25px 10px #6d843480;
}

.scrollToTop:hover .scrollToTop__arrow {
  background-color: #6d8434;
}

.scrollToTop:focus {
  background-color: #e8e8e2;
  outline: none;
  box-shadow: 0 0 25px 10px #6d843480;
}

.scrollToTop:focus .scrollToTop__arrow {
  background-color: #6d8434;
}

.scrollToTop__arrow {
  background-color: #e8e8e2;
  border-radius: 6px;
  width: 8px;
  height: 35px;
  transition: background .3s cubic-bezier(.4, 0, .2, 1);
}

.scrollToTop__arrow:first-child {
  transform: rotate(42deg);
}

.scrollToTop__arrow:last-child {
  transform: rotate(-42deg);
}
